.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #09d3ac;
}

#header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    color: white;
}

.navbar-light .navbar-brand {
    color: white;
}

.navbar-light .navbar-nav .nav-link {
    color: white;
}

.navbar-light .navbar-nav .show>.nav-link, .navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .nav-link.active {
    color: white;
}

.bg-light {
    background-color: #6b44cc !important;
    color: white;
}

#popupDiv{
    /*width:500px;*/
    padding: 10px 10px 10px 10px;
    display: none;
    position: absolute;
    background-color: white;
    /*left: 20vw;*/
    /*right: 20vw;*/
    /*width: 98%;*/
    width: 100%;
    /*left: 1%;*/
    /*right: 1%;*/
    bottom: 0;
    margin: auto;
    border-top: 3px solid #ffcf4b;
    z-index: 2;
    border-radius: 7px;
    overflow: auto;
    max-height: 40vh;
}

.mapdiv {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1;
}

.mapboxgl-ctrl-top-left {
    /*margin-left: 40vw;*/
    margin-top: 50px;
}

